import React, { useState } from 'react';
import { MoreHorizontal, Search, Trash2 } from 'lucide-react';
import DeviceDetailModal from './DeviceDetailModal';
import DeviceForm from './DeviceForm';
import { AnimatePresence, motion } from 'framer-motion';

const mockDevices = [
  {
    id: '1',
    name: 'Defibrilatör',
    brand: 'Philips',
    model: 'HeartStart XL',
    serial: 'SN123456',
    location: 'Yoğun Bakım 1',
    status: 'Çalışıyor',
    lastMaintenance: '2024-05-01',
    qrUrl: 'https://envanter.egemed.com/device/1',
    history: [
      { date: '2024-05-01', type: 'Bakım', note: 'Periyodik bakım yapıldı.' },
      { date: '2024-03-10', type: 'Arıza', note: 'Enerji ünitesi değiştirildi.' },
    ],
  },
  {
    id: '2',
    name: 'Ventilatör',
    brand: 'Dräger',
    model: 'Evita Infinity V500',
    serial: 'SN654321',
    location: 'Yoğun Bakım 2',
    status: 'Bakımda',
    lastMaintenance: '2024-04-15',
    qrUrl: 'https://envanter.egemed.com/device/2',
    history: [
      { date: '2024-04-15', type: 'Bakım', note: 'Filtre değişimi yapıldı.' },
      { date: '2024-02-20', type: 'Arıza', note: 'Alarm sistemi arızası giderildi.' },
    ],
  },
  {
    id: '3',
    name: 'EKG Cihazı',
    brand: 'GE',
    model: 'MAC 2000',
    serial: 'SN789012',
    location: 'Acil Servis',
    status: 'Arızalı',
    lastMaintenance: '2024-03-20',
    qrUrl: 'https://envanter.egemed.com/device/3',
    history: [
      { date: '2024-03-20', type: 'Bakım', note: 'Batarya değişimi yapıldı.' },
      { date: '2024-03-18', type: 'Arıza', note: 'Ekran arızası bildirildi.' },
    ],
  },
];

const statusColor: Record<string, string> = {
  'Çalışıyor': 'bg-green-100 text-green-800 dark:bg-green-900/30 dark:text-green-300',
  'Bakımda': 'bg-yellow-100 text-yellow-800 dark:bg-yellow-900/30 dark:text-yellow-300',
  'Arızalı': 'bg-red-100 text-red-800 dark:bg-red-900/30 dark:text-red-300',
};

interface DeviceTableProps {
  devices?: typeof mockDevices;
}

export default function DeviceTable({ devices }: DeviceTableProps) {
  const [selected, setSelected] = useState<typeof mockDevices[0] | null>(null);
  const [open, setOpen] = useState(false);
  const [editOpen, setEditOpen] = useState(false);
  const [editDevice, setEditDevice] = useState<typeof mockDevices[0] | null>(null);
  const [localDevices, setLocalDevices] = useState(devices);
  const [query, setQuery] = useState('');
  const [deleteOpen, setDeleteOpen] = useState(false);
  const [deleteDevice, setDeleteDevice] = useState<typeof mockDevices[0] | null>(null);
  const [statusFilter, setStatusFilter] = useState('Tümü');

  const statusOptions = ['Tümü', 'Çalışıyor', 'Bakımda', 'Arızalı'];

  React.useEffect(() => {
    setLocalDevices(devices);
  }, [devices]);

  const handleOpen = (device: typeof mockDevices[0]) => {
    setSelected(device);
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
    setSelected(null);
  };
  const handleEdit = (device: typeof mockDevices[0]) => {
    setEditDevice(device);
    setEditOpen(true);
    setOpen(false);
  };
  const handleEditSubmit = (data: any) => {
    setLocalDevices((prev) =>
      prev?.map((d) => (d.id === editDevice?.id ? { ...d, ...data } : d))
    );
    setEditOpen(false);
    setEditDevice(null);
  };
  const handleEditCancel = () => {
    setEditOpen(false);
    setEditDevice(null);
  };
  const handleDelete = (device: typeof mockDevices[0]) => {
    setDeleteDevice(device);
    setDeleteOpen(true);
  };
  const handleDeleteConfirm = () => {
    setLocalDevices((prev) => prev?.filter((d) => d.id !== deleteDevice?.id));
    setDeleteOpen(false);
    setDeleteDevice(null);
  };
  const handleDeleteCancel = () => {
    setDeleteOpen(false);
    setDeleteDevice(null);
  };

  const data = localDevices && localDevices.length > 0 ? localDevices : mockDevices;

  const filtered = data.filter((device) => {
    const q = query.toLowerCase();
    const statusMatch = statusFilter === 'Tümü' || device.status === statusFilter;
    return (
      statusMatch && (
        device.name.toLowerCase().includes(q) ||
        device.brand.toLowerCase().includes(q) ||
        device.model.toLowerCase().includes(q) ||
        device.serial.toLowerCase().includes(q) ||
        device.location.toLowerCase().includes(q) ||
        device.status.toLowerCase().includes(q)
      )
    );
  });

  return (
    <>
      <div className="flex flex-col md:flex-row md:items-center gap-2 mb-4">
        <div className="relative w-full max-w-xs">
          <span className="absolute left-3 top-1/2 -translate-y-1/2 text-muted-foreground">
            <Search size={18} />
          </span>
          <input
            type="text"
            value={query}
            onChange={(e) => setQuery(e.target.value)}
            placeholder="Cihaz adı, marka, model, lokasyon, durum..."
            className="w-full pl-10 pr-3 py-2 rounded-xl border border-border bg-background focus:outline-none focus:ring-2 focus:ring-accent transition shadow-sm"
          />
        </div>
        <select
          value={statusFilter}
          onChange={(e) => setStatusFilter(e.target.value)}
          className="rounded-xl border border-border bg-background px-3 py-2 focus:outline-none focus:ring-2 focus:ring-accent transition shadow-sm text-sm max-w-[140px]"
        >
          {statusOptions.map((opt) => (
            <option key={opt} value={opt}>{opt}</option>
          ))}
        </select>
      </div>
      <div className="overflow-x-auto rounded-2xl border border-border bg-muted/40">
        <table className="min-w-full text-sm">
          <thead>
            <tr className="bg-muted/60">
              <th className="px-4 py-3 text-left font-semibold">Cihaz Adı</th>
              <th className="px-4 py-3 text-left font-semibold">Marka/Model</th>
              <th className="px-4 py-3 text-left font-semibold">Seri No</th>
              <th className="px-4 py-3 text-left font-semibold">Lokasyon</th>
              <th className="px-4 py-3 text-left font-semibold">Durum</th>
              <th className="px-4 py-3 text-left font-semibold">Son Bakım</th>
              <th className="px-4 py-3 text-left font-semibold text-center">İşlemler</th>
            </tr>
          </thead>
          <tbody>
            {filtered.length === 0 ? (
              <tr>
                <td colSpan={7} className="text-center py-8 text-muted-foreground">Kriterlere uygun cihaz bulunamadı.</td>
              </tr>
            ) : (
              filtered.map((device) => (
                <tr key={device.id} className="hover:bg-accent/40 transition-colors">
                  <td className="px-4 py-3 font-medium">{device.name}</td>
                  <td className="px-4 py-3">{device.brand} {device.model}</td>
                  <td className="px-4 py-3">{device.serial}</td>
                  <td className="px-4 py-3">{device.location}</td>
                  <td className="px-4 py-3">
                    <span className={`px-2 py-1 rounded-xl text-xs font-semibold ${statusColor[device.status]}`}>{device.status}</span>
                  </td>
                  <td className="px-4 py-3">{device.lastMaintenance}</td>
                  <td className="px-4 py-3 text-center flex gap-2 justify-center">
                    <button
                      className="p-2 rounded-full hover:bg-muted transition-colors"
                      onClick={() => handleOpen(device)}
                      title="Detaylar"
                    >
                      <MoreHorizontal size={18} />
                    </button>
                    <button
                      className="p-2 rounded-full hover:bg-red-100 dark:hover:bg-red-900/30 transition-colors text-red-600"
                      onClick={() => handleDelete(device)}
                      title="Sil"
                    >
                      <Trash2 size={18} />
                    </button>
                  </td>
                </tr>
              ))
            )}
          </tbody>
        </table>
      </div>
      <DeviceDetailModal open={open} onClose={handleClose} device={selected} onEdit={handleEdit} />
      <AnimatePresence>
        {editOpen && (
          <motion.div
            className="fixed inset-0 z-50 flex items-center justify-center bg-black/40 backdrop-blur-sm"
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
          >
            <motion.div
              className="bg-background rounded-2xl shadow-2xl w-full max-w-lg p-0 relative"
              initial={{ scale: 0.95, y: 40, opacity: 0 }}
              animate={{ scale: 1, y: 0, opacity: 1 }}
              exit={{ scale: 0.95, y: 40, opacity: 0 }}
              transition={{ type: 'spring', stiffness: 300, damping: 30 }}
            >
              <button
                className="absolute top-4 right-4 p-2 rounded-full hover:bg-muted transition-colors"
                onClick={handleEditCancel}
                aria-label="Kapat"
              >
                ×
              </button>
              <DeviceForm initial={editDevice!} onSubmit={handleEditSubmit} onCancel={handleEditCancel} />
            </motion.div>
          </motion.div>
        )}
        {deleteOpen && (
          <motion.div
            className="fixed inset-0 z-50 flex items-center justify-center bg-black/40 backdrop-blur-sm"
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
          >
            <motion.div
              className="bg-background rounded-2xl shadow-2xl w-full max-w-sm p-0 relative"
              initial={{ scale: 0.95, y: 40, opacity: 0 }}
              animate={{ scale: 1, y: 0, opacity: 1 }}
              exit={{ scale: 0.95, y: 40, opacity: 0 }}
              transition={{ type: 'spring', stiffness: 300, damping: 30 }}
            >
              <div className="p-6">
                <h3 className="text-lg font-bold mb-2">Cihazı Sil</h3>
                <p className="mb-4 text-muted-foreground">{deleteDevice?.name} cihazını silmek istediğinize emin misiniz?</p>
                <div className="flex gap-2 justify-end">
                  <button
                    onClick={handleDeleteCancel}
                    className="px-4 py-2 rounded-xl bg-muted text-foreground hover:bg-muted/70 transition"
                  >
                    İptal
                  </button>
                  <button
                    onClick={handleDeleteConfirm}
                    className="px-4 py-2 rounded-xl bg-red-600 text-white font-semibold hover:bg-red-700 transition"
                  >
                    Sil
                  </button>
                </div>
              </div>
            </motion.div>
          </motion.div>
        )}
      </AnimatePresence>
    </>
  );
} 