import React, { useState } from 'react';
import { motion } from 'framer-motion';

const statusOptions = ['Çalışıyor', 'Bakımda', 'Arızalı'];

interface DeviceFormProps {
  initial?: {
    name: string;
    brand: string;
    model: string;
    serial: string;
    location: string;
    status: string;
    lastMaintenance: string;
  };
  onSubmit: (data: any) => void;
  onCancel?: () => void;
}

export default function DeviceForm({ initial, onSubmit, onCancel }: DeviceFormProps) {
  const [form, setForm] = useState(
    initial || {
      name: '',
      brand: '',
      model: '',
      serial: '',
      location: '',
      status: statusOptions[0],
      lastMaintenance: '',
    }
  );
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false);

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError('');
    if (!form.name || !form.brand || !form.model || !form.serial || !form.location || !form.lastMaintenance) {
      setError('Lütfen tüm alanları doldurun.');
      return;
    }
    setLoading(true);
    await new Promise((r) => setTimeout(r, 800)); // Simüle submit
    setLoading(false);
    onSubmit(form);
  };

  return (
    <motion.form
      onSubmit={handleSubmit}
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      exit={{ opacity: 0, y: 20 }}
      className="space-y-4 p-4 md:p-6 rounded-2xl border border-border bg-muted/40 max-w-lg mx-auto"
    >
      <h2 className="text-xl font-bold mb-2">Cihaz {initial ? 'Güncelle' : 'Ekle'}</h2>
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        <div>
          <label className="block text-sm font-medium mb-1">Cihaz Adı</label>
          <input
            name="name"
            value={form.name}
            onChange={handleChange}
            className="w-full rounded-xl border border-border bg-background px-3 py-2 focus:outline-none focus:ring-2 focus:ring-accent transition shadow-sm"
            placeholder="Örn. Defibrilatör"
            required
          />
        </div>
        <div>
          <label className="block text-sm font-medium mb-1">Marka</label>
          <input
            name="brand"
            value={form.brand}
            onChange={handleChange}
            className="w-full rounded-xl border border-border bg-background px-3 py-2 focus:outline-none focus:ring-2 focus:ring-accent transition shadow-sm"
            placeholder="Örn. Philips"
            required
          />
        </div>
        <div>
          <label className="block text-sm font-medium mb-1">Model</label>
          <input
            name="model"
            value={form.model}
            onChange={handleChange}
            className="w-full rounded-xl border border-border bg-background px-3 py-2 focus:outline-none focus:ring-2 focus:ring-accent transition shadow-sm"
            placeholder="Örn. HeartStart XL"
            required
          />
        </div>
        <div>
          <label className="block text-sm font-medium mb-1">Seri No</label>
          <input
            name="serial"
            value={form.serial}
            onChange={handleChange}
            className="w-full rounded-xl border border-border bg-background px-3 py-2 focus:outline-none focus:ring-2 focus:ring-accent transition shadow-sm"
            placeholder="Örn. SN123456"
            required
          />
        </div>
        <div>
          <label className="block text-sm font-medium mb-1">Lokasyon</label>
          <input
            name="location"
            value={form.location}
            onChange={handleChange}
            className="w-full rounded-xl border border-border bg-background px-3 py-2 focus:outline-none focus:ring-2 focus:ring-accent transition shadow-sm"
            placeholder="Örn. Yoğun Bakım 1"
            required
          />
        </div>
        <div>
          <label className="block text-sm font-medium mb-1">Durum</label>
          <select
            name="status"
            value={form.status}
            onChange={handleChange}
            className="w-full rounded-xl border border-border bg-background px-3 py-2 focus:outline-none focus:ring-2 focus:ring-accent transition shadow-sm"
          >
            {statusOptions.map((opt) => (
              <option key={opt} value={opt}>{opt}</option>
            ))}
          </select>
        </div>
        <div className="md:col-span-2">
          <label className="block text-sm font-medium mb-1">Son Bakım Tarihi</label>
          <input
            name="lastMaintenance"
            type="date"
            value={form.lastMaintenance}
            onChange={handleChange}
            className="w-full rounded-xl border border-border bg-background px-3 py-2 focus:outline-none focus:ring-2 focus:ring-accent transition shadow-sm"
            required
          />
        </div>
      </div>
      {error && <div className="text-red-600 text-sm mt-2">{error}</div>}
      <div className="flex gap-2 mt-4 justify-end">
        {onCancel && (
          <button
            type="button"
            onClick={onCancel}
            className="px-4 py-2 rounded-xl bg-muted text-foreground hover:bg-muted/70 transition"
          >
            İptal
          </button>
        )}
        <button
          type="submit"
          disabled={loading}
          className="px-6 py-2 rounded-xl bg-accent text-accent-foreground font-semibold hover:bg-accent/80 transition shadow-sm disabled:opacity-60"
        >
          {loading ? 'Kaydediliyor...' : initial ? 'Güncelle' : 'Ekle'}
        </button>
      </div>
    </motion.form>
  );
} 