'use client';
import DashboardLayout from '@/components/layouts/DashboardLayout';
import DeviceTable from '@/components/DeviceTable';
import DeviceForm from '@/components/DeviceForm';
import React, { useState } from 'react';
import { Plus } from 'lucide-react';
import { AnimatePresence, motion } from 'framer-motion';

export default function DevicesPage() {
  const [open, setOpen] = useState(false);
  const [devices, setDevices] = useState<any[]>([]); // Sadece mock için, gerçek veriyle değişecek

  // DeviceTable'a prop ile veri göndermek için ileride güncellenecek
  const handleAdd = (data: any) => {
    setDevices((prev) => [
      ...prev,
      {
        ...data,
        id: Math.random().toString(36).slice(2),
        qrUrl: 'https://envanter.egemed.com/device/' + Math.random().toString(36).slice(2),
        history: [],
      },
    ]);
    setOpen(false);
  };

  return (
    <DashboardLayout>
      <div className="mb-8 flex flex-col md:flex-row md:items-center md:justify-between gap-4">
        <div>
          <h1 className="text-3xl md:text-4xl font-bold mb-2 tracking-tight">Cihazlar</h1>
          <p className="text-muted-foreground text-lg">Tüm envanter cihazlarınızı görüntüleyin, arayın ve yönetin.</p>
        </div>
        <button
          onClick={() => setOpen(true)}
          className="flex items-center gap-2 px-4 py-2 rounded-xl bg-accent text-accent-foreground font-semibold hover:bg-accent/80 transition shadow-sm"
        >
          <Plus size={18} /> Cihaz Ekle
        </button>
      </div>
      <DeviceTable devices={devices} />
      <AnimatePresence>
        {open && (
          <motion.div
            className="fixed inset-0 z-50 flex items-center justify-center bg-black/40 backdrop-blur-sm"
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
          >
            <motion.div
              className="bg-background rounded-2xl shadow-2xl w-full max-w-lg p-0 relative"
              initial={{ scale: 0.95, y: 40, opacity: 0 }}
              animate={{ scale: 1, y: 0, opacity: 1 }}
              exit={{ scale: 0.95, y: 40, opacity: 0 }}
              transition={{ type: 'spring', stiffness: 300, damping: 30 }}
            >
              <button
                className="absolute top-4 right-4 p-2 rounded-full hover:bg-muted transition-colors"
                onClick={() => setOpen(false)}
                aria-label="Kapat"
              >
                ×
              </button>
              <DeviceForm onSubmit={handleAdd} onCancel={() => setOpen(false)} />
            </motion.div>
          </motion.div>
        )}
      </AnimatePresence>
    </DashboardLayout>
  );
} 