import React from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { X, QrCode } from 'lucide-react';
import QRCodePreview from './QRCodePreview';

interface DeviceDetailModalProps {
  open: boolean;
  onClose: () => void;
  device: {
    id: string;
    name: string;
    brand: string;
    model: string;
    serial: string;
    location: string;
    status: string;
    lastMaintenance: string;
    qrUrl: string;
    history: { date: string; type: 'Bakım' | 'Arıza'; note: string }[];
  } | null;
  onEdit?: (device: any) => void;
}

export default function DeviceDetailModal({ open, onClose, device, onEdit }: DeviceDetailModalProps) {
  if (!device) return null;
  return (
    <AnimatePresence>
      {open && (
        <motion.div
          className="fixed inset-0 z-50 flex items-center justify-center bg-black/40 backdrop-blur-sm"
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          exit={{ opacity: 0 }}
        >
          <motion.div
            className="bg-background rounded-2xl shadow-2xl w-full max-w-lg p-6 relative"
            initial={{ scale: 0.95, y: 40, opacity: 0 }}
            animate={{ scale: 1, y: 0, opacity: 1 }}
            exit={{ scale: 0.95, y: 40, opacity: 0 }}
            transition={{ type: 'spring', stiffness: 300, damping: 30 }}
          >
            <button
              className="absolute top-4 right-4 p-2 rounded-full hover:bg-muted transition-colors"
              onClick={onClose}
              aria-label="Kapat"
            >
              <X size={20} />
            </button>
            <div className="flex items-center gap-4 mb-6">
              <div className="flex items-center justify-center w-16 h-16 rounded-xl bg-muted">
                <QrCode size={32} />
              </div>
              <div className="flex-1">
                <h2 className="text-2xl font-bold mb-1">{device.name}</h2>
                <div className="text-muted-foreground text-sm">{device.brand} {device.model}</div>
                <div className="text-xs text-muted-foreground">Seri No: {device.serial}</div>
              </div>
              {onEdit && (
                <button
                  className="ml-auto px-3 py-1 rounded-xl bg-accent text-accent-foreground text-xs font-semibold hover:bg-accent/80 transition shadow-sm"
                  onClick={() => onEdit(device)}
                >
                  Düzenle
                </button>
              )}
            </div>
            <div className="mb-4">
              <div className="flex flex-wrap gap-2 mb-2">
                <span className="px-2 py-1 rounded-xl text-xs font-semibold bg-muted">{device.location}</span>
                <span className="px-2 py-1 rounded-xl text-xs font-semibold bg-accent text-accent-foreground">{device.status}</span>
                <span className="px-2 py-1 rounded-xl text-xs font-semibold bg-muted">Son Bakım: {device.lastMaintenance}</span>
              </div>
              <div className="flex items-center gap-2 mt-2">
                <QRCodePreview url={device.qrUrl} size={80} />
                <a
                  href={device.qrUrl}
                  target="_blank"
                  rel="noopener noreferrer"
                  className="ml-2 text-blue-600 dark:text-blue-400 underline text-xs"
                >
                  Cihaz Sayfasını Aç
                </a>
              </div>
            </div>
            <div>
              <h3 className="font-semibold mb-2">Geçmiş Bakım/Arıza Kayıtları</h3>
              <ul className="space-y-1 max-h-32 overflow-y-auto pr-2">
                {device.history.map((h, i) => (
                  <li key={i} className="text-xs flex gap-2 items-center">
                    <span className="font-mono text-muted-foreground">{h.date}</span>
                    <span className={`px-2 py-0.5 rounded text-white text-xs ${h.type === 'Bakım' ? 'bg-green-500' : 'bg-red-500'}`}>{h.type}</span>
                    <span>{h.note}</span>
                  </li>
                ))}
              </ul>
            </div>
          </motion.div>
        </motion.div>
      )}
    </AnimatePresence>
  );
} 